import axios from 'axios';
import { AnyAction, Dispatch } from 'redux';
import { ThunkAction } from 'redux-thunk';
import * as jomeActionTypes from '../actionTypes/homeActionTypes';
type ThunkResult<R> = ThunkAction<any, {}, undefined, AnyAction>;

export const fectchSucess = () =>({
    type:jomeActionTypes.FECTCH_LOCATION_SUCESS,
})

export const fectchFailure = () =>({
    type:jomeActionTypes.FECTCH_LOCATION_FAILURE,
})

export const fectchLocation = ():ThunkResult<void>  =>{
    return async(dispatch:Dispatch) =>{
        try{
            dispatch({type:jomeActionTypes.FECTCH_LOCATION_LOADING,});
            const res = await axios.post('https://demo.smartstorez.com/ourgeniedemo/tempapi/locations/');
            console.log(res.data);
            if(res.data.statusMessage === "success"){
                dispatch({type:jomeActionTypes.ADD_LOCATION,
                    payload:res.data.result[0],
                })
            }
            if(res.data.statusMessage === "failed"){
                console.log(res.data.result.error_message);
                dispatch({type:jomeActionTypes.FECTCH_LOCATION_FAILURE,message:res.data.result.error_message})
            }
        }catch(err){
            console.error(err);
        }
    }
}