import {
    Grid,TextField,
    Typography, InputAdornment,
    MenuItem,
    Box
} from '@material-ui/core';
import Carousel from 'react-multi-carousel';
import 'react-multi-carousel/lib/styles.css';
import UAParser from "ua-parser-js";
import SearchIcon from '@material-ui/icons/Search';
import useStyles from './bannerStyles';
import { useEffect, useState } from 'react';
import { connect } from 'react-redux';
import { ThunkDispatch } from 'redux-thunk';
import { fectchLocation } from '../../../redux/actions/homeAction';
const responsive = {
    desktop: {
        breakpoint: {
            max: 3000,
            min: 1024
        },
        items: 1
    },
    mobile: {
        breakpoint: {
            max: 464,
            min: 0
        },
        items: 1
    },
    tablet: {
        breakpoint: {
            max: 1024,
            min: 464
        },
        items: 1
    }
};
const Banner = (props) => {
    const classes = useStyles();
    const [locations,setLocations] = useState<any []>([]);
    useEffect(()=>{
        props.fetchLocation()    
    },[])
   
    return (
        <Carousel
            additionalTransfrom={0}
            arrows
            centerMode={false}
            containerClass="container"
            draggable
            focusOnSelect={false}
            infinite
            keyBoardControl
            minimumTouchDrag={80}
            sliderClass=""
            slidesToSlide={1}
            swipeable
            responsive={responsive}
        >
            {[1, 2, 3, 4, 5].map(() => (
                <Box component='div' className={classes.homeBanner}>
                    <Grid container className={classes.bannerContent}>
                        <Grid item className={classes.bannerHeading}>
                                <Typography variant="h3" style={{ fontWeight: 600 }}>A nice section heading goes here</Typography>
                                <Typography>Lorem ipsum dolor sit amet, consetetur sadipscing elitr, sed </Typography>
                        </Grid>
                        <Grid item className={classes.bannerForm}>
                                <TextField select className={classes.locationSelect}
                                    InputProps={{
                                        className: classes.input,
                                        placeholder:"select city"
                                    }}
                                >
                                    {props.state.homeReducer.locations && props.state.homeReducer.locations .length > 0 ? 
                                        props.state.homeReducer.locations .map((location) => (
                                        <MenuItem  key={location.id} value={location.location_name}>
                                            {location.location_name}
                                            </MenuItem>
                                        )) : <MenuItem value="location not found">
                                        location not found
                                        </MenuItem>
                                    }
                                </TextField>
                                <TextField className={classes.locationInput}
                                    InputProps={{
                                        className: classes.input,
                                        placeholder: "search for a service",
                                        startAdornment: (
                                            <InputAdornment position="start">
                                                <SearchIcon />
                                            </InputAdornment>
                                        ),
                                    }}
                                >
                                </TextField>
                            </Grid>
                        </Grid>
                </Box>
                ))
                }
        </Carousel>
    )
};
//mapping the redux store state to the component
const mapStateToProps = (states: any) => {
    return {
        state: states
    }
}

//mapping the redux dispatch action to the component
const mapDispatchToProps = (dispatch: ThunkDispatch<{}, {}, any>) => {
    return ({
        fetchLocation:() =>dispatch(fectchLocation()),
    })
}
export default connect(mapStateToProps,mapDispatchToProps) (Banner);